@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");
* {
  margin: 0%;
  padding: 0%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Manrope", sans-serif;
}

body {
  background-color: #191e28;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

#root {
  width: 100%;
}

.card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background-color: #2b364a;
  text-align: center;
  width: 40%;
  margin: auto;
  position: relative;
  padding: 2rem;
  border-radius: 1rem;
}

.card h3 {
  color: #52ffa8;
  margin: 1rem auto;
}

.card h1 {
  color: #cee3e9;
  text-align: center;
  min-width: 100%;
}

.card h1::first-letter {
  color: #52ffa8;
}

.card .dividerContainer {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 2rem auto;
  width: 100%;
}

.card .dividerContainer img {
  width: 100%;
}

.diceContainer {
  width: 4rem;
  height: 4rem;
  background-color: #52ffa8;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border-radius: 50%;
  position: absolute;
  bottom: -2rem;
  -webkit-transition: -webkit-box-shadow 350ms ease;
  transition: -webkit-box-shadow 350ms ease;
  transition: box-shadow 350ms ease;
  transition: box-shadow 350ms ease, -webkit-box-shadow 350ms ease;
}

.diceContainer:hover {
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 30px 5px #52ffa8;
          box-shadow: 0px 0px 30px 5px #52ffa8;
}

.refresh {
  -webkit-animation: rotateDice 1s infinite forwards;
          animation: rotateDice 1s infinite forwards;
}

@-webkit-keyframes rotateDice {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotateDice {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.attribution {
  font-size: 12px;
  text-align: center;
  position: absolute;
  bottom: 1rem;
}

.attribution a {
  color: #52ffa8;
}

@media screen and (max-width: 1000px) {
  .card {
    width: 70%;
  }
}

@media screen and (max-width: 500px) {
  .card {
    width: 95%;
    padding: 1rem;
  }
}
