$bg-color: #191e28;
$card-bg-color: #2b364a;
$number-clor: hsl(193, 38%, 86%);
$neon-green: hsl(150, 100%, 66%);
$text-color: hsl(193, 38%, 86%);
@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");

$fontStyle: 'Manrope', sans-serif;
* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: $fontStyle;
}

body {
  background-color: $bg-color;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
#root{
    width: 100%;
}

.card {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: $card-bg-color;
    text-align: center;
    width: 40%;
    margin: auto;
    position: relative;
    padding: 2rem;
    border-radius: 1rem;
  h3 {
    color: $neon-green;
    margin: 1rem auto;
  }

  h1 {
    color: $text-color;
    text-align: center;
    min-width: 100%;
    &::first-letter{
        color: $neon-green;
        // font-size: larger;
    }
  }

  .dividerContainer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2rem auto;
    width: 100%;
    img{
        width: 100%;
    }
  }
}
.diceContainer{
    width: 4rem;
    height: 4rem;
    background-color: $neon-green;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    position: absolute;
    bottom: -2rem;
    transition: box-shadow 350ms ease;
    &:hover{
        cursor: pointer;
        box-shadow: 0px 0px 30px 5px $neon-green;
    }
}
.refresh{
animation: rotateDice 1s infinite forwards;
}
@keyframes rotateDice {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
// attribution
.attribution {
  font-size: 12px;
  text-align: center;
  position: absolute;
    bottom: 1rem;
}
.attribution a {
  color: $neon-green;
}


@media screen and (max-width: 1000px) {
    .card{
        width: 70%;
        
    }
}
@media screen and (max-width: 500px) {
    .card{
        width: 95%;
        padding: 1rem;
    }
}